*{
    box-sizing: border-box;
    margin: 0%;
    padding:0;
}
.GalleryApp{
     width:100%;
     min-height: 100vh;
     background: none;
     display:flex;
}
img{
    display:block;
    cursor:pointer;
}
.Container{
    margin:auto;
    width:100%;
    max-width: 540px;
}
.Container .selected{
    width:300px;
    height:300px;
    margin:0 auto;
    border:4px solid black;
}
.Container .imgContainer{
    width:100%;
    display:flex;
    justify-content:space-between;
    flex-wrap:wrap;
    padding: 40px 0 0 0;
}

.Container .imgContainer img{
    width:250px;
     height:250px;
     margin:5px 0;
}